import React from "react";
import "./NotFound.css";
import { Link } from "react-router-dom";

const NotFound = () => {
  return (
    <div className="not_found">
      <Link to="/">
        <img
          className="img_top"
          src="https://images-na.ssl-images-amazon.com/images/G/01/error/title._TTD_.png"
          alt="Image_not_found"
        />
        <img
          className="img_bottom"
          src="https://images-na.ssl-images-amazon.com/images/G/01/error/170._TTD_.jpg"
          alt="dog"
        />
      </Link>
    </div>
  );
};

export default NotFound;
